import React, { Component } from 'react';var divStyle = {    width: '1100px',    height: '400px',    display : 'inline-block',    marginRight : '10px'};class SlipGrid extends Component {  render(){    return (      <div>          <br/>          <button type="button" id="btnDelete">삭제</button>          <br/>          <div id="realgrid" style={divStyle}></div>      </div>    );  }};export default SlipGrid;